// Resume Download Styles
.resume-download {
  width: 100%;
  padding: 10px;
  background-color: $color-primary-3;
  @extend .flex-box-center;
  flex-direction: column;
  color: #FFF;
  text-align: center;
  @include card-depth(3);
  .resume-header {
    margin-top: 30px; }

  .resume-buttons {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
    button.resume-btn {
      width: 225px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 30px;
      padding: 10px;
      font-size: 180%;
      background-color: rgba(0,0,0,0.8);
      color: #FFF;
      border-radius: 25px;
      border: 1px solid white;
      font-family: 'Nixie One', sans-serif;
      box-shadow: 1px 2px 5px rgba(0,0,0,1);
      transition: all 150ms ease-in-out;
      &:hover, &:active {
        box-shadow: 2px 5px 10px rgba(0,0,0,1);
        background-color: rgba(255,255,255,0.8);
        color: #000; } }

    @media (max-width: 900px) {
      button.resume-btn {
        width: auto;
        margin: 10px;
        font-size: 1.8em;
        padding-left: 15px;
        padding-right: 15px; } }
    @media (max-width: 700px) {
      flex-direction: column;
      justify-content: center;
      padding: 10px;
      button.resume-btn {
        width: 225px;
        padding: 15px;
        margin: 20px 0px; } } } }
