.gallery {
  width: 100%; }
.gallery .flexslider {
  margin: auto auto;
  max-width: 850px; }

/*
 *Video Slider Styles */
/* ====================================================================================================================
 * FONT-FACE
 * ==================================================================================================================== */
@font-face {
  font-family: 'flexslider-icon';
  src: url('fonts/flexslider-icon.eot');
  src: url('fonts/flexslider-icon.eot?#iefix') format("embedded-opentype"), url('fonts/flexslider-icon.woff') format("woff"), url('fonts/flexslider-icon.ttf') format("truetype"), url('fonts/flexslider-icon.svg#flexslider-icon') format("svg");
  font-weight: normal;
  font-style: normal; }


/* ====================================================================================================================
 * RESETS
 * ==================================================================================================================== */

.flex-container a:hover, .flex-slider a:hover, .flex-container a:focus, .flex-slider a:focus {
  outline: none; }

.slides {
  margin: 0;
  padding: 0;
  list-style: none;
  > li {
    margin: 0;
    padding: 0;
    list-style: none; } }

.flex-control-nav, .flex-direction-nav {
  margin: 0;
  padding: 0;
  list-style: none; }

.flex-pauseplay span {
  text-transform: capitalize; }

/* ====================================================================================================================
 * BASE STYLES
 * ==================================================================================================================== */

#3d-slider {
  background-color: $grey-0;
  width: 40%;
  height: 100%; }

.slider-wrapper {
  overflow-x: hidden;
  background-color: $grey-0;
  width: 100%;
  height: 100%; }

.flexslider {
  margin: 0;
  padding: 0;
  max-height: 100vh;
  .slides {
    > li {
      display: none;
      -webkit-backface-visibility: hidden; }
    img {
      width: 100%;
      display: block; }
    &:after {
      content: "\0020";
      display: block;
      clear: both;
      visibility: hidden;
      line-height: 0;
      height: 0; } } }

html[xmlns] .flexslider .slides {
  display: block; }

* html .flexslider .slides {
  height: 1%;
  background-color: $grey-0; }

.no-js .flexslider .slides > li:first-child {
  display: block; }

/* ====================================================================================================================
 * DEFAULT THEME
 * ==================================================================================================================== */

.flexslider {
  margin: 0 0 40px;
  background: $grey-0;
  border: 0px;
  position: relative;
  zoom: 1;
  -webkit-box-shadow: "" 0 1px 4px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: "" 0 1px 4px rgba(0, 0, 0, 0.2);
  -o-box-shadow: "" 0 1px 4px rgba(0, 0, 0, 0.2);
  box-shadow: "" 0 1px 4px rgba(0, 0, 0, 0.2);
  .slides {
    zoom: 1;
    img {
      height: auto; } } }

.flex-viewport {
  max-height: 2000px;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -ms-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease; }

.loading .flex-viewport {
  max-height: 300px; }

.carousel li {
  margin-right: 5px; }

.flex-direction-nav {
  *height: 0;
  a {
    text-decoration: none;
    display: block;
    width: 40px;
    height: 40px;
    margin: -20px 0 0;
    position: absolute;
    top: 50%;
    z-index: 10;
    overflow: hidden;
    opacity: 0;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.8);
    text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.3);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    &:before {
      font-family: "flexslider-icon";
      font-size: 40px;
      display: inline-block;
      content: '\f001';
      color: rgba(255, 255, 255, 0.8);
      text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.3); }
    &.flex-next:before {
      content: '\f002'; } }
  .flex-prev {
    left: -50px; }
  .flex-next {
    right: -50px;
    text-align: right; } }

.flexslider:hover .flex-direction-nav {
  .flex-prev {
    opacity: 0.7;
    left: 10px;
    &:hover {
      opacity: 1; } }
  .flex-next {
    opacity: 0.7;
    right: 10px;
    &:hover {
      opacity: 1; } } }

.flex-direction-nav .flex-disabled {
  opacity: 0 !important;
  filter: alpha(opacity = 0);
  cursor: default; }

.flex-pauseplay a {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: 5px;
  left: 10px;
  opacity: 0.8;
  z-index: 10;
  overflow: hidden;
  cursor: pointer;
  color: $grey-0;
  &:before {
    font-family: "flexslider-icon";
    font-size: 20px;
    display: inline-block;
    content: '\f004'; }
  &:hover {
    opacity: 1; }
  &.flex-play:before {
    content: '\f003'; } }

.flex-control-nav {
  width: 100%;
  position: absolute;
  bottom: -40px;
  text-align: center;
  li {
    margin: 0 6px;
    display: inline-block;
    zoom: 1;
    *display: inline; } }

.flex-control-paging li a {
  width: 11px;
  height: 11px;
  display: block;
  background: $grey-2;
  cursor: pointer;
  text-indent: -9999px;
  -webkit-box-shadow: inset 0 0 3px rgba(255, 255, 255, 0.3);
  -moz-box-shadow: inset 0 0 3px rgba(255, 255, 255, 0.3);
  -o-box-shadow: inset 0 0 3px rgba(255, 255, 255, 0.3);
  box-shadow: inset 0 0 3px rgba(255, 255, 255, 0.3);
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  &:hover {
    background: $grey-2; }
  &.flex-active {
    background: $grey-0;
    cursor: default; } }

.flex-control-thumbs {
  margin: 5px 0 0;
  position: static;
  overflow: hidden;
  li {
    width: 25%;
    float: left;
    margin: 0; }
  img {
    width: 100%;
    height: auto;
    display: block;
    opacity: .7;
    cursor: pointer;
    -webkit-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -ms-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
    &:hover {
      opacity: 1; } }
  .flex-active {
    opacity: 1;
    cursor: default; } }

/* ====================================================================================================================
 * RESPONSIVE
 * ==================================================================================================================== */
@media screen and (max-width: 860px) {
  .flex-direction-nav {
    .flex-prev {
      opacity: 1;
      left: 10px; }
    .flex-next {
      opacity: 1;
      right: 10px; } } }

@media screen and (max-width: 450px), screen and (max-width: 700px) and (orientation: landscape) {
  .flexslider .flex-control-nav {
    visibility: hidden; } }
